import { BasicColumn, FormProps } from '/@/components/Table';
import { defHttp } from '/@/utils/http/axios';
import dayjs from 'dayjs';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getOrgInfoUrl = '/bi/common/org/info',

  getStationUrl = '/baseset/common/list-station',

  getLossDetailUrl = '/bi/elec/lossDetail',

  exportExcelUrl = '/bi/elec/export-loss-detail',
}

//导出
export const exportExcel = (params) =>
  defHttp.post(
    { url: Api.exportExcelUrl, params, responseType: 'blob' },
    { isReturnNativeResponse: true },
  );

export const getLossDetailData = (params) =>
  defHttp.get({ url: Api.getLossDetailUrl, params }, { isOnlyResult: true });

export const getOrgInfoData = (params) =>
  defHttp.get({ url: Api.getOrgInfoUrl, params }, { isOnlyResult: true });

export const getStationData = (params) => {
  return defHttp.post({ url: Api.getStationUrl, params });
};

export const searchFormSchema: Partial<FormProps> = {
  labelWidth: 120,
  actionColOptions: {
    span: 24,
  },
  autoAdvancedLine: 1,
  showAdvancedButton: true,
  baseColProps: {
    span: 7,
  },
  autoSubmitOnEnter: true,
  noHideBtnFlag: true,
  schemas: [
    {
      field: `dateCycle`,
      label: `时间维度`,
      component: 'Select',
      required: true,
      defaultValue: 'year',
      componentProps: {
        options: [
          { value: 'year', label: '年份' },
          { value: 'month', label: '月份' },
          { value: 'day', label: '自然日' },
        ],
      },
    },
    {
      field: `yearValue`,
      label: `年份`,
      component: 'YearPicker',
      defaultValue: dayjs(new Date()).format('YYYY'),
      componentProps: {
        format: 'YYYY',
        valueFormat: 'YYYY',
        style: { width: '100%' },
      },
      show: ({ values }) => {
        return values.dateCycle == 'year';
      },
      dynamicRules: ({ values }) => {
        return values.dateCycle == 'year' ? [{ required: true, message: '年份必填' }] : [];
      },
    },
    {
      field: `[startMonth, endMonth]`,
      label: `月份`,
      component: 'RangePicker',
      componentProps: {
        picker: 'month',
        format: 'YYYY-MM',
        valueFormat: 'YYYY-MM',
        style: { width: '100%' },
        placeholder: ['开始月份', '结束月份'],
      },
      show: ({ values }) => {
        return values.dateCycle == 'month';
      },
      dynamicRules: ({ values }) => {
        return values.dateCycle == 'month' ? [{ required: true, message: '月份必填' }] : [];
      },
    },
    {
      field: `[startDate, endDate]`,
      label: `时间段`,
      component: 'RangePicker',
      componentProps: {
        format: 'YYYY-MM-DD',
        valueFormat: 'YYYY-MM-DD',
        style: { width: '100%' },
        placeholder: ['开始时间', '结束时间'],
      },
      show: ({ values }) => {
        return values.dateCycle == 'day';
      },
      dynamicRules: ({ values }) => {
        return values.dateCycle == 'day' ? [{ required: true, message: '时间段必填' }] : [];
      },
    },
    {
      field: `orgId`,
      label: `所属公司`,
      component: 'ApiSelect',
      componentProps: {
        options: [],
        api: getOrgInfoData,
        placeholder: '请选择所属公司',
      },
      show: useUserStore().isMultiOrganization,
    },
    {
      field: `stationId`,
      label: `电站名称`,
      component: 'ApiSelect',
      componentProps: {
        options: [],
        api: async () => {
          const sts = await getStationData({});
          const map: any = [];
          sts.result.forEach((e) => {
            map.push({ value: e.powerStationId, label: e.powerStationName });
          });

          return map;
        },
        placeholder: '请输入电站名称',
      },
    },
    {
      field: `lossType`,
      label: `损失类型`,
      component: 'DictSelectBox',
      componentProps: {
        type: 'elecLossCate',
        placeholder: '请选择损失类型',
      },
    },
    {
      field: `lossReason`,
      label: `损失原因`,
      component: 'DictSelectBox',
      componentProps: {
        type: 'elecLossType',
        placeholder: '请选择损失原因',
      },
    },
    {
      field: `equipmentName`,
      label: `设备名称`,
      component: 'Input',
      componentProps: { placeholder: '请输入设备名称' },
    },

    {
      field: `factoryName`,
      label: `生产厂家`,
      component: 'Input',
      componentProps: {
        autocomplete: 'off',
        placeholder: '请输入生产厂家',
      },
    },
  ],
};

